import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { CusViewHttpService } from '../../../http/custom-view/custom-view.http.service';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Message } from "primeng/primeng";
import { FjDetailListBean } from './bean/foujue-detail.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
// import {BussType,LoanVetoType,LoanSubVetoType,RefuseReason,LoanOperationType,LoanOperationLink} from '../../../constant/custView.codeValue';
@Component({
    selector: 'foujue-detail',
    templateUrl: './foujue-detail.component.html',
    styleUrls: ['./foujue-detail.component.scss']
})
export class FouJueDetailComponent implements OnInit, OnChanges {
    @Output() outValue = new EventEmitter();
    @Input('in-value') inValue: any;
    msgs: Message[] = [];
    custNo: string;
    bussTypeOptions: any = [];//业务类型
    loanVetoTypeOpyions: any = [];//贷款否决类型
    loanSubVetoTypeOptions: any = [];//贷款否决二级否决类型
    refuseReasonOptions: any = [];//贷款否决拒绝原因
    loanOperationTypeOptions: any = [];//贷款否决操作类型
    loanOperationLinkOptions: any = [];//操作环节  否决环节
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    fjDetailList: FjDetailListBean = new FjDetailListBean();

    subVetoTypeList: any = [];//二级否决类型列表
    code: any;
    constructor(
        public httpService: CusViewHttpService,
        public commfunc: Commfunc,
    ) {
        this.codeVal();
        this.bussTypeOptions = this.code['BussType'];
        this.loanVetoTypeOpyions = this.code['LoanVetoType'];
        this.loanSubVetoTypeOptions = this.code['LoanSubVetoType'];
        this.refuseReasonOptions = this.code['RefuseReason'];
        this.loanOperationTypeOptions = this.code['LoanOperationType'];
        this.loanOperationLinkOptions = this.code['LoanOperationLink'];
        //-------------------------------------上面是码值
    }

    ngOnInit() {
    }
    ngOnChanges() {
        if (this.inValue && this.inValue != '') {
            let param = {
                number: this.inValue.number
            }
            this.httpService.BZ_0200300000106_LOAN(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.fjDetailList = data;
                    this.subVetoTypeList = [];
                    let array = data.subVetoType.split(',');
                    let flag = true;
                    array.forEach((item, i) => {
                        if (item == null || item == "") {
                            return flag = false;
                        }
                        if (flag) {
                            this.subVetoTypeList.push(item);
                        }
                    });
                    console.log(this.fjDetailList.vetoerName)
                    let item = {
                        tellerId: this.fjDetailList.vetoerName
                    }
                    this.httpService.queryTellerById(item).subscribe(data => {
                        this.fjDetailList.personName = data.tellerName;
                        console.log(this.fjDetailList.personName)
                    })
                    let temp = {
                        flag: "0",
                        orgId: this.fjDetailList.appBranchNo,
                        rowNum: "10",
                        startRow: "1",
                    }
                    this.httpService.queryOrgByCombConditions(temp).subscribe(data => {
                        if (data.orgInfoList && data.orgInfoList.length > 0) {
                            this.fjDetailList.appBranchNo = data.orgInfoList[0].orgName;
                        }
                    })
                } else {
                    data.returnCode.message ? data.returnCode.message : '调用服务失败';
                    this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                }
            });
        }
    }
    close() {
        this.outValue.emit(false);
    }
    //码值
    codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
}
