import { Component, OnInit,Input,Output,EventEmitter,OnChanges } from '@angular/core';
import { CusViewHttpService } from '../../../http/custom-view/custom-view.http.service';
import { MdzxxBean } from '../../bean/custom-view.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
// import {ALL_IDEN_TYPE,AppStep,RegisterType,TranStatus,TzbankClientFlag,ApproveStatuss,BussType,SubBussType,GuaranteeMode,TranLoanFlag,EffLoanFlag,PayType,CreditType,LoanPurpuseCode,HandleResult,SubVetoType} from '../../../constant/custView.codeValue';

@Component({
  selector: 'app-list-details',
  templateUrl: './list-details.component.html',
  styleUrls: ['./list-details.component.scss']
})
export class ListDetailsComponent implements OnInit,OnChanges {
  @Output() outValue = new EventEmitter();
  @Input('in-value') inValue: any;
  @Input('key-label') keyLabel: any;
  @Input('kind-do') kindDo: string = '';
  mdzxxBean:MdzxxBean=new MdzxxBean();
  approveStatusOptions:any[]=[];//审批状态
  appStepOptions:any[]=[];//审批阶段
  registerTypeOptions:any[]=[];//信息录入方式
  tranStatusOptions:any[]=[];//名单制状态
  tzbankClientFlagOptions:any[]=[];//是否我行客户
  allIdenTypeOptions:any[]=[];
  code:any;
  constructor(
    public httpService:CusViewHttpService,
    public commfunc: Commfunc,
  ) {
    this.codeVal();
    this.approveStatusOptions=this.code['ApproveStatuss'];
    this.appStepOptions=this.code['AppStep'];
    this.registerTypeOptions=this.code['RegisterType'];
    this.tranStatusOptions=this.code['TranStatus'];
    this.tzbankClientFlagOptions=this.code['TzbankClientFlag'];
    this.allIdenTypeOptions=this.code['ALL_IDEN_TYPE'];
    //---------------------------------------上面是码值
   }

  ngOnInit() {
    delete this.allIdenTypeOptions[0];
    this.allIdenTypeOptions=this.allIdenTypeOptions;
  }
  ngOnChanges(){
    if(this.inValue&&this.inValue!=''){
      let param={
        pk:this.inValue.pk
      }
      this.httpService.BZ_1300300000102_LOAN(param).subscribe(data=>{
        if(data.returnCode.code=='success'){
          this.mdzxxBean=data;
          this.httpService.queryOrgById({orgId:this.mdzxxBean.appBranchNo}).subscribe(data=>{
            this.mdzxxBean.appBranchNo=data.orgName;
          });
          this.httpService.queryOrgById({orgId:this.mdzxxBean.orgId}).subscribe(data=>{
            this.mdzxxBean.orgId=data.orgName;
          });
          this.httpService.queryTellerById({ tellerId: this.mdzxxBean.contactManagerId }).subscribe(data => {
            this.mdzxxBean.contactManagerId = data.tellerName;
          });
          this.httpService.queryTellerById({ tellerId: this.mdzxxBean.appUserId }).subscribe(data => {
            this.mdzxxBean.appUserId = data.tellerName;
          });
        }
      })
    }
  }
  close(){
    this.outValue.emit(false);
  }
  //码值
  codeVal() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code= ret //code是自己声明的变量
  }
}
