import { Component, OnInit,Input,Output,EventEmitter,OnChanges } from '@angular/core';
import { CusViewHttpService } from '../../../http/custom-view/custom-view.http.service';
import { tableMessage } from 'app/@uisftech/common/constant/config';
@Component({
  selector: 'app-risk-level',
  templateUrl: './risk-level.component.html',
  styleUrls: ['./risk-level.component.scss']
})
export class RiskLevelComponent implements OnInit,OnChanges {
  @Output() outValue = new EventEmitter();
  @Input('in-value') inValue: any;
  @Input('key-label') keyLabel: any;
  @Input('kind-do') kindDo: string = '';
  custNo:string;
  fxdjtz:any[]=[];
  tabMesg:any = tableMessage;//表格中无数据时显示的内容
  constructor(
    public httpService:CusViewHttpService
  ) {
   }

  ngOnInit() {
  }
  ngOnChanges(){
    if(this.inValue&&this.inValue!=''){
      let param={
        pk:this.inValue.pk
      }
      this.httpService.BZ_1300300000103_LOAN(param).subscribe(data=>{
        if(data.returnCode.code=='success'){
          this.fxdjtz=data.infoList;
        }
      })
    }
  }
  close(){
    this.outValue.emit(false);
  }
}
