  //跳转征信  外部
  zxwbPart() {
    let param = {
      custNo: this.custNo
    }
    this.httpService.selectCustBriefInfo(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.certType = data.idenType;
        this.certID = data.idenNum;
        this.custAttr = data.custAttribute;
        this.custName = data.custName;
        this.custterm = data.custType;
        if (data.dataSourceId == 'BR00A') {
          this.HTTPIPDress = TZB_HTTP_XIN;
        } else if (data.dataSourceId == 'BR00B') {
          this.HTTPIPDress = TZB_HTTP_XIN_C;
        } else if (!data.dataSourceId || data.dataSourceId == '') {
          this.HTTPIPDress = TZB_HTTP_XIN;
        }
        let temp = {
          handleFlag: '1',
          securdStr: this.userId,
          strategy: '1',
        }
        this.httpService.securdStrService(temp).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.param = data.securdStr;
          }
          else {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }]
          }
        }, err => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        });
        let item = {
          handleFlag: '1',
          securdStr: this.userId + this.custNo + this.custName + this.certID,
          strategy: '2',
        }
        this.httpService.securdStrService(item).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.key = data.securdStr;
          }
          else {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }]
          }
        }, err => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
    });
  }
