  //资金中介交易查询
  selectCapitalAgencyTxn() {
    let param = {
      custNo: this.custNo,
      pageNum: this.zjzjjywlspageNum,
      pageSize: this.zjzjjywlspageSize
    }
    this.httpService.selectCapitalAgencyTxn(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        data.infoList.forEach(item => {
          this.httpService.queryOrgById({ orgId: item.txnOrgId }).subscribe(data => {
            if (data.orgName != null) {
              item.txnOrgId = data.orgName;
            }
          });
        });
        this.zjzjjywls = data.infoList;
        this.zjzjjywlspageNum = data.pageNum;
        this.zjzjjywlspageSize = data.pageSize;
        this.zjzjjywlstotalNum = data.totalNum;
      } else {
        data.returnCode.message ? data.returnCode.message : '调用服务失败';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    });
  }

  paginatezjzjjywls(event) {
    this.zjzjjywlspageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.zjzjjywlspageNum = currentPage;
    this.selectCapitalAgencyTxn();
  }
