  //贷款业务全选按钮
  checkAllClick() {
    this.chooseData = [];
    for (let i = 0; i < this.mdzzlxrs.length; i++) {
      if (this.checkAll == true) {
        this.mdzzlxrs[i].checkBox = true;
        this.chooseData.push(this.mdzzlxrs[i]);
      }
      else {
        this.chooseData = [];
        this.mdzzlxrs[i].checkBox = false;
      }
    }
  }
  //贷款业务单选按钮
  checkBoxClick(i, data) {
    if (data.checkBox) {
      this.chooseData.push(data)
    } else {
      this.chooseData.forEach((item, i) => {
        if (item == data) {
          this.chooseData.splice(i, 1)
        }
      });
    }
    if (this.chooseData.length == this.mdzzlxrs.length) {
      this.checkAll = true;
    } else {
      this.checkAll = false;
    }
  }
