  //评分
  queryPf() {
    let param = {
      clientNo: this.custNo,
      clientName: this.custName,
      globalType: this.idenType,
      globalId: this.idenNum,
      // clientNo:'2301072321',
      // globalType:'0',
      // globalId:'362123197112251230',
      // clientName:'王建华 ',
    }
    this.httpService.BZ_0200300003807_CSS(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        if (data.precardArray.length > 0) {
          data.precardArray.forEach(item => {
            if (item.applyAmt == '' || item.applyAmt == null) {
              item.applyAmt = 0;
            }
          });
        }
        if (data.appcardArray.length > 0) {
          data.appcardArray.forEach(item => {
            if (item.applyAmt == '' || item.applyAmt == null) {
              item.applyAmt = 0;
            }
          });
        }
        if (data.aftercardArray.length > 0) {
          data.aftercardArray.forEach(item => {
            if (item.applyAmt == '' || item.applyAmt == null) {
              item.applyAmt = 0;
            }
          });
        }
        this.precardArray = data.precardArray;
        this.appcardArray = data.appcardArray;
        this.aftercardArray = data.aftercardArray;
      } else {
        data.returnCode.message ? data.returnCode.message : '调用服务失败';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    })
  }
