  //征信信息查询
  queryZxxxInfo() {
    let num = '';
    let perId = '';
    let temp = {
      // custNo: "1300002163"
      custNo: this.custNo
    }
    this.httpService.selectCustBriefInfo(temp).subscribe(data => {
      if (data.returnCode.code == 'success') {
        num = data.custType;
        if (num == '1') {
          this.itemParam = 'P';//个人
          this.showReson = false;
          perId = data.idenNum;
          let param = {
            globalId: perId,
            clientType: this.itemParam
          }
          this.httpService.BZ_1100300008903_CIQS(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
              data.infoList.forEach(item => {
                this.httpService.queryTellerById({ tellerId: item['queryName'] }).subscribe(data => {
                  if (data.tellerName && data.tellerName != '' && data.tellerName != null) {
                    item['queryName'] = data.tellerName;
                  }
                });
              });
              this.zxxxInfo = data.infoList;
            } else {
              data.returnCode.message ? data.returnCode.message : '调用服务失败';
              this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
          });
        } else if (num == '2') {
          this.itemParam = 'C';
          this.showReson = true;
          this.httpService.selectComCustBasicInfo(temp).subscribe(data => {
            if (data.returnCode.code == 'success') {
              perId = data.signature;
              if (!perId || perId == '' || perId == null) {
                return;
              }
              let param = {
                globalId: perId,
                clientType: this.itemParam
              }
              this.httpService.BZ_1100300008903_CIQS(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                  data.infoList.forEach(item => {
                    this.httpService.queryTellerById({ tellerId: item['queryName'] }).subscribe(data => {
                      if (data.tellerName && data.tellerName != '' && data.tellerName != null) {
                        item['queryName'] = data.tellerName;
                      }
                    });
                  });
                  this.zxxxInfo = data.infoList;
                } else {
                  data.returnCode.message ? data.returnCode.message : '调用服务失败';
                  this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                }
              });
            } else {
              data.returnCode.message ? data.returnCode.message : '调用服务失败';
              this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }

          })
        }

      } else {
        data.returnCode.message ? data.returnCode.message : '调用服务失败';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }

    });
  }
