  // 办税密码申请
  getTaxPassword() {
    //校验的
    for (const i in this.taxForm.controls) {
      this.taxForm.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.taxForm.controls) {
      if (!this.taxForm.controls[i].valid) {
        return;
      }
    }
    let applyTime = new Date();
    let tranDate = this.dateToString(applyTime);
    let branchId = '';
    if (this.commfunc.getSessionData('commonHeader')) {
      branchId = JSON.parse(this.commfunc.getSessionData('commonHeader')).orgId
    }

    let mapMessage = {
      CUST_NO: this.custNo,
      TAX_PAYER_NUM: this.passwordForTax.taxpayerIdentNum,
      IDEN_NUM: this.passwordForTax.legalRepresentNum
    }

    let param = {
      tranDate: tranDate,
      channelType: '01',
      branchId: branchId,
      msgTempleTranCode: 'MSG_WSBSFWMM_APPLY',
      recPhoneNo: this.passwordForTax.recPhoneNo,
      map: {
        CUST_NO: this.custNo,
        TAX_PAYER_NUM: this.passwordForTax.taxpayerIdentNum,
        IDEN_NUM: this.passwordForTax.legalRepresentNum
      }
    }

    this.httpService.messageSend(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.retMsg = data.retMsg;
        this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
      } else {
        data.returnCode.message ? data.returnCode.message : '调用服务失败';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    })
  }

